/* Подключение шрифтов */
@import url("https://fonts.googleapis.com/css2?family=Unbounded:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@400;700&display=swap");

/* Подключение Tailwind CSS */
@import "tailwindcss/base";
@import "tailwindcss/components";
@import "tailwindcss/utilities";

/* Дополнительные стили для подсветки синтаксиса */
@import "highlight.js/styles/default.css";

/* Основные стили для body */
body {
  font-family: "Unbounded", sans-serif;
  background-color: #1a2027; /* Темно-синий фон */
  color: #ffffff; /* Яркий белый текст */
  margin: 0;
  padding: 0;
  line-height: 1.75;
}

/* Стили для абзацев */
p {
  font-family: "Nunito Sans", sans-serif;
  font-size: 1.125rem;
  color: #ffffff; /* Светло-серый текст */
  margin-top: 1rem;
  margin-bottom: 1rem;
}

/* Основные стили для контейнера приложения */
.app {
  max-width: 960px;
  margin: auto;
  padding: 20px;
}

/* Стили для списка постов */
.post-list {
  margin-bottom: 20px;
}

/* Стили для отдельного поста */
.post {
  margin-bottom: 40px;
}

/* Стили для названия поста */
.post-name {
  color: #61dafb;
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 1rem;
}

/* Стили для заголовков */
h1 {
  color: #61dafb;
  font-size: 2.5rem; /* Увеличен размер шрифта для h1 */
  font-weight: bold;
  margin-bottom: 1rem;
}

h2 {
  color: #61dafb;
  font-size: 2rem; /* Размер шрифта для h2 */
  font-weight: bold;
  margin-bottom: 1rem;
}

h3 {
  color: #61dafb;
  font-size: 1.5rem; /* Уменьшен размер шрифта для h3 */
  font-weight: bold;
  margin-bottom: 1rem;
}

/* Стили для ссылок */
a {
  color: #61dafb;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

/* Стили для списков */
ul, ol {
  list-style-position: outside; /* Вынесены маркеры за границы контейнера списка */
  padding-left: 1.5rem;
}

/* Стили для маркера списка */
li::marker {
  color: #999999; /* Серый маркер */
}

/* Стили для цитат */
blockquote {
  border-left: 4px solid #61dafb;
  padding-left: 1rem;
  font-style: italic;
  color: #cccccc; /* Светло-серый текст */
  margin-top: 1rem;
  margin-bottom: 1rem;
}

/* Дополнительные стили для заголовка */
.main-header {
  color: #f9d41c;
  font-size: 2.5rem;
  font-weight: bold;
}

.main-header-link {
  color: #f9d41c;
  font-size: 2.5rem;
  font-weight: bold;
}

.main-header-link:hover {
  text-decoration: none;
}

/* Стили для таблиц */
table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 1rem;
  margin-bottom: 1rem;
}

th, td {
  border: 2px solid #61dafb;
  padding: 0.5rem;
  color: #ffffff; /* Яркий белый текст в ячейках таблицы */
}

th {
  background-color: #61dafb;
  color:#282c35;
}

tbody {
  font-family: 'Nunito Sans', sans-serif;
}

/* Обновленная цветовая палитра */
.bg-primary {
  background-color: #1a2027; /* Темно-синий фон */
  color: #ffffff; /* Яркий белый текст */
}

.bg-secondary {
  background-color: #282c35; /* Темно-серый фон */
  color: #cccccc; /* Светло-серый текст */
}

.bg-accent {
  background-color: #61dafb; /* Голубой акцентный цвет */
  color: #1a2027; /* Темный текст */
}

.bg-code {
  background-color: #282c35; /* Темный фон для кода */
  color: #ffffff; /* Яркий белый текст для кода */
}

ul {
  list-style-type: disc;
  padding-left: 1.5rem;
}

li::marker {
  color: #46656d;
}

ol {
  list-style-type: decimal;
  padding-left: 1.5rem;
}

li {
  margin-bottom: 5px;
  font-family: 'Nunito Sans', sans-serif;
  font-size: 1.1em;
}

.home-link {
  font-size: 14px;
  margin-right: 10px;
  cursor: pointer;
  padding: 8px 12px;
  border: none;
  background-color: #f9d41c; /* Желтый цвет для активной кнопки */
  color: #1a2027; /* Темный текст для активной кнопки */
  transition: background-color 0.3s ease, color 0.3s ease;
}

/* may not work */

.post-name {
  margin-right: 10px;
}

.sort-buttons {
  margin-bottom: 10px;
}

.sort-button {
  font-size: 14px;
  margin-right: 10px;
  cursor: pointer;
}

.post-last-modified {
  display: block;
  color: #999; /* Темно-серый текст для времени последнего изменения */
  margin-top: 5px;
  text-align: right;
}

.post-list-ul {
  list-style-type: none;
  padding-left: 0%;
}

/* Добавленные стили для ссылок в посте */
.post-list-ul a:hover {
  text-decoration: none; /* Убираем подчеркивание при наведении на ссылку в посте */
}
/* Добавленный стиль для подчеркивания названия поста при наведении */
.post-list-ul a:hover .post-name {
  text-decoration: underline;
}

/* Обновленный стиль для времени без подчеркивания */
.post-list-ul a:hover .post-last-modified {
  text-decoration: none;
}

/* Обновленные стили для активных кнопок сортировки */
.sort-button.active {
  background-color: #f9d41c; /* Желтый цвет для активной кнопки */
  color: #1a2027; /* Темный текст для активной кнопки */
}

/* Обновленные стили для кнопок сортировки */
.sort-button {
  font-size: 14px;
  margin-right: 10px;
  cursor: pointer;
  padding: 8px 12px;
  border: none;
  background-color: #282c35; /* Темно-серый фон */
  color: #ffffff; /* Светло-белый текст */
  transition: background-color 0.3s ease, color 0.3s ease;
}

/* Дополнительный стиль для активной кнопки */
.sort-button.active {
  background-color: #f9d41c; /* Голубой акцентный цвет */
  color: #1a2027; /* Темный текст */
}

/* Дополнительные стили для контейнера сайта */
.site-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Разделитель между названием сайта и контентом */
.divider {
  width: 100%;
  height: 2px;
  background-color: #f9d41c; /* Голубой акцентный цвет */
  margin: 20px 0;
}
