.hljs {
    display: block;
    overflow-x: auto;
    padding: 0.5em;
    color: #ffffff; /* Яркий белый текст для кода */
    background: #282c35; /* Темный фон для кода */
    border-radius: 0.6rem;
    margin-top: 1rem;
    margin-bottom: 1rem;
  }
  
  .hljs-comment,
  .hljs-quote {
    color: #546e7a; /* Серый цвет для комментариев и цитат */
  }
  
  .hljs-keyword,
  .hljs-selector-tag,
  .hljs-subst,
  .hljs-literal,
  .hljs-section,
  .hljs-link,
  .hljs-number,
  .hljs-meta,
  .hljs-regexp,
  .hljs-variable,
  .hljs-template-variable,
  .hljs-tag,
  .hljs-name,
  .hljs-selector-id,
  .hljs-selector-class {
    color: #c792ea; /* Фиолетовый цвет для ключевых слов и тегов */
  }
  
  .hljs-string,
  .hljs-title,
  .hljs-symbol,
  .hljs-bullet,
  .hljs-attribute,
  .hljs-link {
    color: #89ddff; /* Светло-голубой цвет для строк, заголовков и атрибутов */
  }
  
  .hljs-built_in,
  .hljs-builtin-name,
  .hljs-meta,
  .hljs-type,
  .hljs-selector-attr,
  .hljs-selector-pseudo,
  .hljs-addition,
  .hljs-variable,
  .hljs-template-variable {
    color: #f78c6c; /* Оранжевый цвет для встроенных функций и переменных */
  }
  
  .hljs-deletion,
  .hljs-formula {
    color: #f07178; /* Красный цвет для удалений и формул */
  }
  
  .hljs-selector-combinator,
  .hljs-selector-separator,
  .hljs-selector-pseudo {
    color: #82aaff; /* Голубой цвет для комбинаторов селекторов и псевдоэлементов */
  }
  .hljs-tag {
    color: #82aaff; /* Цвет для тегов */
  }
  
  .hljs-attr {
    color: #c792ea; /* Цвет для атрибутов */
  }
  
  .hljs-string {
    color: #8ddde8; /* Цвет для строк */
  }
  
  .hljs-keyword {
    color: #c792ea; /* Цвет для ключевых слов */
  }